
.col-md-7{
	display: inline-block;
	width: 65%;
}
.controllerCenter .controllerSys div{
	display: inline-block;
	margin: 10px 100px 10px 0;
}
.table-hover{
	border-collapse: collapse;
	text-align: center;
}
.controllerCenter div input{
	width: 30px;
	height: 30px;
	font-size: 20px;
}
.callBedTable{
	line-height: 26px;
	padding-bottom: 25px;
}
.callBedTable tr:hover{
	background: #e9e9e9;
}
.callBedTable thead th{
	border: 1px solid #2c3a54;
}
.callBedTable tr td{
	border: 1px solid #b4caed;
	padding: 0;
}
.callBedTable table thead{
	background-color: #425574;
	color: #ffffff;
}
.callBedTable td input{
	font-size: 16px;
	height: 20px;
	text-align: center;
}
.callBedTable td select{
	height: 26px;
}
.callBedTable td button{
	height: 25px;
  width: 115px;
  background-color: #7b93f6;
  color: white;
}
/*.controllerBtn{
	text-align: center;
}*/
.controllerBtn span{
	display: inline-block;
	width: 200px;
	height: 50px;
	font-size: 20px;
	font-weight: bold;
	cursor: pointer;
	font-family: cursive;
	color: #fff;
	line-height: 50px;
	text-align: center;
}
.devRegister{
	background: #61b834;
}
.getRestOver{
	background: #006DCC;
}
.over-bedNo{
	position: relative;
	top: 100px;
	text-align: center;
}
.input-over-bedNo{
	width: 100px !important;
	font-size: 22px !important;
}
.restOverPopup{
	left: 0px !important;
	top: 22px !important;
}



