
body{
	margin: 0;
	padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 0;
}
ul{
	margin-top: 0;
}
.app-title{
	width: 100%;
	height: 66px;
	position: relative;
	top: 0;
	background-image: linear-gradient(to top, #39496a, #4a5f89);
	color: white;
}
.homeName{
	position: relative;
	left: 200px;
	font-size: 24px;
	font-family: "微软雅黑";
	line-height: 66px;
}
.app-time{
	display: inline-block;
	position: relative;
	left: 300px;
	top: 6px;
	text-align: center;
	margin-top: 1px;
}
nav{
	height: 65px;
	position: relative;
	background: #425575;
	color: white;
}
nav a{
	cursor: pointer;
  transition: all 0.18s ease-out 0s;
  position: relative;
	width: 118px;
  line-height: 65px;
  display: inline-block;
  text-align: center;
}
nav a span{
	color: white;
}
.headNavLine{
  border-left: 1px solid #2c3a54;
  border-right: 1px solid #566a8e;
  display: inline-block;
  height: 39px;
  position: relative;
  top: 15px;
}
a:link {
  text-decoration: none;
  color: #F60;
}
a:visited {
  text-decoration: none;
}
a:hover {
	text-decoration: none;
}
a:active {
	text-decoration: none;
	font-size: 14px;
	font-weight: bold;
}
.head div{
	background: #415378;
	color: white;
	font-size: 20px;
	height: 32px;
}
.manually-list-info{
	margin-left: 20px;
}
.isCallBed{
	background: #0be417;
}
.notCallBed{
	background: red;
}
.rightList{
	margin-bottom: 20px;
	border: 1px solid #039;
}
.manually-time{
	display: inline-block;
	margin-right: 5px;
}
.mainTable{
	width: 30%;
	display: inline-block;
	position: absolute;
	top: 300px;
}
.btn-md{
	border-radius: 5px;
}
.callBed div input{
	width: 90px;
	height: 25px;
	font-size: 18px;
	text-align: center;
	border-radius: 3px;
}
.btn-lg{
	display: inline-block;
    height: 30px;
    width: 150px;
    border-radius: 5px;
   	border: 1px solid;
    text-align: center;
    line-height: 30px;
    cursor: pointer;
    color: #fff;
}
.btn-sm{
	display: inline-block;
    height: 26px;
    width: 38%;
    border-radius: 5px;
   	border: 1px solid;
    text-align: center;
    line-height: 26px;
    cursor: pointer;
    color: #fff;
}
.btn-info{
	background-color: #5bc0de;
    border-color: #46b8da;
}
.btn-success{
	background-color: #5cb85c;
    border-color: #5cb85c;
}
.btn-table{
	height: 23px;
	line-height: 26px;
}



