
.popup{
  width: 600px;
  height: 600px;
  position: absolute;
  border: 1px solid;
  left: 20%;
  top: 200px;
  background: white;
  z-index: 11111;
}
.modal-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1040;
    background-color: #000000;
    opacity: 0.8;
}
[class^="icon-"], [class*=" icon-"] {
    display: inline-block;
    width: 14px;
    height: 14px;
    margin-top: 1px;
    line-height: 14px;
    vertical-align: text-top;
    background-image: url(../../img/glyphicons-halflings.png);
    background-position: 14px 14px;
    background-repeat: no-repeat;
}
.icon-white{
	background-image: url(../../img/glyphicons-halflings-white.png);
}
.icon-remove {
    background-position: -312px 0;
    right: 0;
    top: 0;
    position: absolute;
    cursor: pointer;
}
.popup .head {
    background-color: #39496a;
    margin-top: -8px;
    padding-bottom: 5px;
    padding-top: 5px;
    color: #ffffff;
    height: 20px;
}
.popup-left{
	display: inline-block;
	width: 30%;
	margin: 20px 0 20px 20px;
	float: left;
}
.popup-right{
	display: inline-block;
	width: 50%;
	margin: 30px 0;
	float: right;
}
.popup-left div{
	margin: 10px 0;
}
.prompt{
	margin: 20px 0 !important;
	font-size: 18px;
	font-weight: bold;
}
.btn-md{
	width: 100px;
	height: 32px;
	margin: 10px;
	border-radius: 5px;
	background: #5bc0de;
}



